import React, { Component, Fragment } from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronLeft, faChevronRight } from '@fortawesome/free-solid-svg-icons'

export default class Showcase extends Component {
  static defaultProps = {
    defaultDescription: "asdfasdf default description",
    data: []
  }

  constructor(props) {
    super(props)
    this.state = {
      activeEl: 0
    }

    this.onLeft = this.onLeft.bind(this);
    this.onRight = this.onRight.bind(this);
  }

  onLeft(event) {
    this.setState({activeEl: Math.max(0, this.state.activeEl-1)})
  }

  onRight(event) {
    this.setState({activeEl: Math.min(this.props.data.length-1, this.state.activeEl+1)})
  }

  render() {
    const {activeEl} = this.state
    const data = this.props.data

    const item = this.props.data[activeEl]

    return (
      <Fragment>
        {/* left */}
        <div className="w-full md:w-1/2 flex items-center">
          <FontAwesomeIcon icon={faChevronLeft} className={(activeEl === 0 ? "text-gray-400" : "text-teal-500 hover:text-teal-600") + " text-3xl m-16 flex-shrink"} onClick={this.onLeft} />
          <div className="flex-grow relative" style={{paddingBottom: "100%"}}>
            <img className="absolute h-full w-full object-scale-down" src={item.img} />
          </div>
          <FontAwesomeIcon icon={faChevronRight} className={(activeEl === data.length-1 ? "text-gray-400" : "text-teal-500 hover:text-teal-600") + " text-3xl m-16 flex-shrink"} onClick={this.onRight} />
        </div>
        {/* right */}
        <div className="w-full md:w-1/2 p-12">
          <h2 className="text-3xl mb-4">{item.title}</h2>
          <ul className="font-light mb-4">
            {item.info.map((li, i) => (
              <li key={`info_${i}`}>{li}</li>
            ))}
          </ul>
          <hr className="my-6" />
          icons to scroll through origin/certs/productinfo/eventfeed
        </div>
      </Fragment>
    )
  }
}
